import axios from 'axios';
// import Qs from 'qs';

const api = process.env.VUE_APP_API;
const path = process.env.VUE_APP_PATH;

/*
|--------------------------------------------------------------------------
| 
|--------------------------------------------------------------------------
|
*/

// products
export const apiGetAllProducts = () => axios.get(`${api}/api/${path}/products/all`); // 
export const apiGetSingleProduct = (id) => axios.get(`${api}/api/${path}/product/${id}`); // 

// 
export const apiGetCart = () => axios.get(`${api}/api/${path}/cart`); // 
export const apiAddToCart = (data) => axios.post(`${api}/api/${path}/cart`, data); // 
